$ft-link-height: 3vw;
$ft-border-radius: 1vw;

.family-tree {
	width: 100%;

	& ul {
		padding-top: 2 * $ft-link-height;
		position: relative;
		transition: all 0.5s;
		display: flex;
	}

	& li {
		display: block;
		flex-grow: 1;
		flex-basis: 0;
		width: 10vw;
		float: left;
		text-align: center;
		list-style-type: none;
		position: relative;
		padding: $ft-link-height 1vw 0;
		transition: all 0.5s;
	}

	& li::after,
	& li::before {
		content: '';
		position: absolute;
		top: 0;
		right: 50%;
		border-top: 1px solid #ccc;
		width: 50%;
		height: $ft-link-height;
	}

	& li::after {
		right: auto;
		left: 50%;
		border-left: 1px solid #ccc;
	}

	& li:only-child::after,
	& li:only-child::before {
		display: none;
	}

	& li:only-child {
		padding-top: 0;
	}

	& li:first-child::before,
	& li:last-child::after {
		border: 0 none;
	}

	& li:last-child::before {
		border-right: 1px solid #ccc;
		border-radius: 0 $ft-border-radius 0 0;
	}

	& li:first-child::after {
		border-radius: $ft-border-radius 0 0 0;
	}

	& ul ul::before {
		content: '';
		position: absolute;
		top: 0;
		left: 50%;
		border-left: 1px solid #ccc;
		width: 0;
		height: 2 * $ft-link-height;
	}
}

