.hamburger {

    background: rgba(0, 0, 0, 0) none repeat scroll 0% 0%;
    display: inline-block;
    height: 16px;
    position: relative;
    vertical-align: middle;
    width: 22px;

    &__line {
        background: #fff none repeat scroll 0% 0%;
        height: 2px;
        left: 0px;
        position: absolute;
        transition: all 0.2s ease 0s;
        width: 100%;

        &:first-child {
            top: 0%;
            transform-origin: left top 0;
        }

        &:nth-child(2) {
            margin-top: -1px;
            top: 50%;
        }

        &:last-child {
            bottom: 0%;
            transform-origin: left bottom 0;
        }

        &_type_active {

            &:first-child {
                transform: translate(3px, -0.5px) rotate(45deg);
            }

            &:nth-child(2) {
                opacity: 0;
                transform: scale(0.5);
                visibility: hidden;
            }

            &:last-child {
                transform: translate(3px, 0.5px) rotate(-45deg);
            }
        }
    }

    &:hover &__line {
        background: $yellow;
    }
}

