@mixin gradient-horizontal($start-color: #555, $end-color: #333, $start-percent: 0%, $end-percent: 100%) {
    background-image: linear-gradient(to right, $start-color $start-percent, $end-color $end-percent); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($start-color)}', endColorstr='#{ie-hex-str($end-color)}', GradientType=1); // IE9 and down
}

.timeline-history {
    position: relative;
    left: 0px;
    width: 100%;
    height: 700px;
    padding: 100px 0px 350px;
    box-sizing: content-box;
    overflow: hidden;

    &__map {
        //background: url("../img/roadmap.jpg") no-repeat top center;
        //background-size: contain;
        width: 100%;
        height: 100%;
    }

    &__map-image {
        width: 100%;
    }

    &__information {
        position: relative;
        text-align: center;
        transition: all .5s linear;
        opacity: 0;
        color: $light-black;

        .paragraph {
            font-size: .75rem;
        }
    }
}

.timeline {
    position: relative;
    z-index: 3;
    top: 0px;
    left: 40%;
    height: 1px;
    width: 1px;
    margin-bottom: 100px;

    &.is-animate {
        .timeline__inner {
            transform: translate3d(-270px, 0, 0);
            width: 3100px;
            transition: width 4s ease, transform 1.5s ease;
        }

        .timeline__year {
            opacity: 1;
            transform: translate3d(0, 0, 0);

            &:hover:before {
                color: $yellow;
                width: 1px;
                height: 20px;
            }

            &:nth-child(2) {
                transition-delay: 0.1s;
            }
            &:nth-child(3) {
                transition-delay: 0.2s;
            }
            &:nth-child(4) {
                transition-delay: 0.3s;
            }
            &:nth-child(5) {
                transition-delay: 0.4s;
            }
            &:nth-child(6) {
                transition-delay: 0.5s;
            }
            &:nth-child(7) {
                transition-delay: 0.6s;
            }
            &:nth-child(8) {
                transition-delay: 0.7s;
            }
        }

        .timeline__year_type_full {
            cursor: pointer;
            color: $yellow;
            font-size: 1rem;
            transition: font-size .3s ease 0s !important;
            top: -35px;

            &:before {
                top: 30px;
            }
        }

    }

    &__inner {
        position: absolute;
        top: 0px;
        left: 0px;
        height: 1px;
        width: 1px;
        //background: #333;
        @include gradient-horizontal($start-color: $menu-color-yellow, $end-color: $grey, $start-percent: 0%, $end-percent: 25%);
        transition: width 2s ease, transform 1.5s ease;
    }

    &__year {
        position: absolute;
        bottom: 0px;
        width: 35px;
        height: 30px;
        font-weight: normal;
        font-size: 0.75rem;
        letter-spacing: 0.15em;
        transform: translate3d(0, -100%, 0);
        opacity: 0;
        transition: all 0.3s ease;

        &:before {
            content: "";
            position: absolute;
            bottom: -4.5px;
            left: 0px;
            width: 5px;
            height: 10px;
            background: #333;
            transition: width .2s linear, height .2s linear;
        }

        &:first-child {
            transform: none;
            opacity: 1;
        }

        &:first-child:before {
            background: none repeat scroll 0% 0% $yellow;
            border-radius: 25%;
            height: 20px;
            left: -10px;
            top: 20px;
            width: 20px;
        }

        &:nth-child(2) {
            transition-delay: 1.4s;
            left: 48px;
        }
        &:nth-child(3) {
            transition-delay: 1.3s;
            left: 105px;
        }
        &:nth-child(4) {
            transition-delay: 1.2s;
            left: 168px;
        }
        &:nth-child(5) {
            transition-delay: 1.1s;
            left: 240px;
        }
        &:nth-child(6) {
            transition-delay: 1s;
            left: 320px;
        }
        &:nth-child(7) {
            transition-delay: 0.9s;
            left: 410px;
        }
        &:nth-child(8) {
            transition-delay: 0.8s;
            left: 505px;
        }
    }

    &__text {
        height: 200px;
    }

}


